.list {
  $self: &;
  margin-bottom: 30px;

  &__add-button {
    span {
      color: #767676;
    }

    &:hover {
      span {
        color: black;
      }
    }
  }

  &__remove-icon {
    opacity: 0;

    &:hover {
      opacity: 0, 8;
    }
  }

  li {
    display: flex;
    align-items: center;
    padding: 10px 12px;
    cursor: pointer;
    transition: background-color 0.15s ease-in-out;

    &:hover #{$self}__remove-icon {
      opacity: 0.9;
    }

    span {
      flex: 1;
    }

    &:hover {
      background: rgba(255, 255, 255, 0.5);
    }

    &.active {
      background-color: #ffffff;
      box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.04);
      border-radius: 4px;
    }

    .badge {
      position: relative;
      left: 5px;
    }

    i {
      display: inline-flex;
      margin-right: 8px;
    }
  }
}
